import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { RouterModule } from "@angular/router";
import { FlexLayoutModule } from '@angular/flex-layout';
import { TranslateModule } from 'ng2-translate/ng2-translate';
import { 
  MatSidenavModule,
  MatListModule,
  MatTooltipModule,
  MatOptionModule,
  MatSelectModule,
  MatMenuModule,
  MatSnackBarModule,
  MatGridListModule,
  MatToolbarModule,
  MatIconModule,
  MatButtonModule,
  MatRadioModule,
  MatCheckboxModule,
  MatCardModule,
  MatProgressSpinnerModule,
  MatRippleModule,
  MatDialogModule,
  MatSlideToggleModule,
  MatInputModule
} from '@angular/material';
import {SlideshowModule} from 'ng-simple-slideshow';
import { AppLoaderModule } from './services/app-loader/app-loader.module';

// ONLY REQUIRED FOR **SIDE** NAVIGATION LAYOUT
import { HeaderSideComponent } from './components/header-side/header-side.component';
import { SidebarSideComponent } from './components/sidebar-side/sidebar-side.component';

// ONLY REQUIRED FOR **TOP** NAVIGATION LAYOUT
import { HeaderTopComponent } from './components/header-top/header-top.component';
import { SidebarTopComponent } from './components/sidebar-top/sidebar-top.component';

// ONLY FOR DEMO (Removable without changing any layout configuration)
import { CustomizerComponent } from './components/customizer/customizer.component';

// ALL TIME REQUIRED 
import { AdminLayoutComponent } from './components/layouts/admin-layout/admin-layout.component';
import { AuthLayoutComponent } from './components/layouts/auth-layout/auth-layout.component';
import { NotificationsComponent } from './components/notifications/notifications.component';
import { SidenavComponent } from './components/sidenav/sidenav.component';
import { BreadcrumbComponent } from './components/breadcrumb/breadcrumb.component';
import { SongItemComponent } from './components/songitem/songitem.component';
import { SongCreditPopupComponent } from './components/songitem/songcreditpopup/songcreditpopup.component';
import { SongItemPopupComponent } from './components/songitem/songitempopup/songitempopup.component';
import { NewsItemComponent } from './components/newsitem/newsitem.component';
import { FooterComponent } from './components/footer/footer.component';
import { MusicPlayer } from './components/musciplayer/musicplayer.component';
import { BottomPlayer } from './components/bottomplayer/bottomplayer.component';
import { PlaylistTablePopupComponent } from '../views/admin/playlist/playlistpopup/playlist-table-popup.component';

// DIRECTIVES
import { FontSizeDirective } from './directives/font-size.directive';
import { ScrollToDirective } from './directives/scroll-to.directive';
import { AppDropdownDirective } from './directives/dropdown.directive';
import { DropdownAnchorDirective } from './directives/dropdown-anchor.directive';
import { DropdownLinkDirective } from './directives/dropdown-link.directive';

// PIPES
import { RelativeTimePipe } from './pipes/relative-time.pipe';
import { ExcerptPipe } from "./pipes/excerpt.pipe";

// SERVICES
import { ThemeService } from './services/theme.service';
import { LayoutService } from './services/layout.service';
import { NavigationService } from "./services/navigation.service";
import { RoutePartsService } from './services/route-parts.service';
import { AuthGuard } from './services/auth/auth.guard';
import { AdminAuthGuard } from './services/auth/admin-auth.guard';
import { FilterService } from './filter.service';
import { FeaturedService } from './services/featured.service';
import { ArtistService } from './services/admin/artists.service';
import { AuthenticationService } from './services/authentication.service';
import { UserService } from './services/admin/users.service';
import { NewsService } from './services/admin/news.service';
import { PlaylistService } from './services/admin/playlist.service';
import { SongsService } from './services/admin/songs.service';
import { AboutService } from './services/admin/about.service';
import { DashboardService } from './services/admin/dashboard.service';
import { PlacementService } from './services/admin/placement.service';
import { LocationService } from './services/location.service';

const classesToInclude = [
  
  HeaderTopComponent,
  SidebarTopComponent,
  SidenavComponent,
  NotificationsComponent,
  SidebarSideComponent,
  HeaderSideComponent,
  AdminLayoutComponent,
  AuthLayoutComponent,
  BreadcrumbComponent,
  CustomizerComponent,
  FontSizeDirective,
  ScrollToDirective,
  AppDropdownDirective,
  DropdownAnchorDirective,
  DropdownLinkDirective,
  RelativeTimePipe,
  ExcerptPipe,
  SongItemPopupComponent,
  SongCreditPopupComponent,
  SongItemComponent,  
  NewsItemComponent,
  FooterComponent,
  MusicPlayer,
  BottomPlayer,
  PlaylistTablePopupComponent
]

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    FlexLayoutModule,
    TranslateModule,
    MatSidenavModule,
    MatListModule,
    MatTooltipModule,
    MatOptionModule,
    MatSelectModule,
    MatMenuModule,
    MatSnackBarModule,
    MatGridListModule,
    MatToolbarModule,
    MatIconModule,
    MatButtonModule,
    MatRadioModule,
    MatCheckboxModule,
    MatCardModule,
    MatProgressSpinnerModule,
    MatRippleModule,
    MatDialogModule,
    SlideshowModule,
    AppLoaderModule,
    MatSlideToggleModule,
    MatInputModule
  ],
  providers: [
    ThemeService,
    LayoutService,
    NavigationService,
    RoutePartsService,
    AuthGuard,
    AdminAuthGuard,
    FilterService,
    FeaturedService,
    ArtistService,
    AuthenticationService,
    UserService,
    NewsService,
    PlaylistService,
    SongsService,
    AboutService,
    DashboardService,
    PlacementService,
    LocationService
  ],
  declarations: classesToInclude,
  exports: classesToInclude,
  entryComponents: [
    SongItemPopupComponent,
    SongCreditPopupComponent,
    PlaylistTablePopupComponent
  ]
})
export class SharedModule { }



