import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from './../authentication.service';

import 'rxjs/add/operator/delay';


@Injectable()
export class PlaylistService {
  header: HttpHeaders;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {
    this.setHeader();
  }

  setHeader() {
    let currentUser = this.authenticationService.currentUserValue;
    if (currentUser && currentUser.user_token) {
        this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
    }
  }

  getAll() {
    return this.http.get(environment.apiURL + 'admin/playlist/playlist');
  }

  getItem(playlist_id) {
    return this.http.get(environment.apiURL + 'admin/playlist/playlistitem/' + playlist_id);
  }

  addItem(data) {
    return this.http.post(environment.apiURL + 'admin/playlist/playlist', data, {headers: this.header})
  }

  addItemWithSongs(data) {
    return this.http.post(environment.apiURL + 'admin/playlist/playlistwithitem', data, {headers: this.header})
  }

  updateItem(playlist_id, data) {
    return this.http.patch(environment.apiURL + 'admin/playlist/playlist/' + playlist_id, data, {headers: this.header});
  }

  removeItem(playlist_id) {
    return this.http.delete(environment.apiURL + 'admin/playlist/playlist/' + playlist_id, {headers: this.header})
  }

  uploadPlaylistThumb(data) {
    return this.http.post(environment.apiURL + 'admin/playlist/playlistthumb', data, {headers: this.header})
  }

  changeFeature(playlist_id) {
    return this.http.post(environment.apiURL + 'admin/playlist/changefeatured/' + playlist_id, {headers: this.header})
  }
}



