import { Injectable } from '@angular/core';
@Injectable()
export class PlayerService {
    public currentSong;
    public isPlaying;

    playlist = [];
    
    constructor() { }

    addPlaylist(songItem) {
        if(this.playlist.filter(playlistItem => playlistItem.song_id ==  songItem.song_id).length == 0) {
            
            var seconds = Math.floor(songItem.song_duration);
            var mins = Math.floor(seconds / 60);
            seconds = seconds % 60;
            songItem['song_duration_text'] = (mins < 10 ? ('0' + mins) : mins) + ':' + (seconds < 10  ? ('0' + seconds) : seconds);
            this.playlist.push(songItem);
        }
    }

    clearPlaylist() {
        this.playlist = [];
    }

    deletePlayListItem(songItem) {
        this.playlist = this.playlist.filter(playListItem => playListItem.song_id != songItem.song_id);
    }
}


