import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User } from '../../models/user';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from './../authentication.service';
import 'rxjs/add/operator/delay';


@Injectable()
export class ArtistService {
  header: HttpHeaders;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {
    this.setHeader();
  }

  setHeader() {
    let currentUser = this.authenticationService.currentUserValue;
    if (currentUser && currentUser.user_token) {
        this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
    }
  }

  getAll() {
    return this.http.get<User[]>(environment.apiURL + 'admin/artist/artists');
  }

  getArtist(artist_id) {
    return this.http.get<User[]>(environment.apiURL + 'admin/artist/artist/' + artist_id);
  }

  addItem(data) {
    return this.http.post(environment.apiURL + 'admin/artist/artist', data, {headers: this.header})
  }

  updateItem(artist_id, data) {
    return this.http.patch(environment.apiURL + 'admin/artist/artist/' + artist_id, data, {headers: this.header});
  }

  removeItem(artist_id) {
    return this.http.delete(environment.apiURL + 'admin/artist/artist/' + artist_id, {headers: this.header})
  }

  uploadAvatar(data) {
    return this.http.post(environment.apiURL + 'admin/artist/artistavatar', data, {headers: this.header})
  }

  changeFeature(artist_id) {
    return this.http.post(environment.apiURL + 'admin/artist/changefeatured/' + artist_id, {headers: this.header})
  }

  changeOrders(data) {
    return this.http.patch(environment.apiURL + 'admin/artist/changeorder/', data, {headers: this.header})
  }
}



