import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from '../../../environments/environment';

import 'rxjs/add/operator/delay';


@Injectable()
export class LocationService {

  constructor(
    private http: HttpClient
  ) { }

  getCountries() {
    return this.http.get(environment.apiURL + 'location/countries');
  }

  getStates(country_id) {
    return this.http.get(environment.apiURL + 'location/states/' + country_id);
  }

  getCities(state_id) {
    return this.http.get(environment.apiURL + 'location/cities/' + state_id);
  }
}


