import { Component, OnInit, OnDestroy, Input, NgZone } from '@angular/core';
import * as WaveSurfer from 'wavesurfer.js';
import * as randomString from 'random-string';
import { MatDialogRef, MatDialog, MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';

import { AuthenticationService } from '../../services/authentication.service';
import { PlayerService } from '../../services/player.service';
import { AppLoaderService } from '../../services/app-loader/app-loader.service';
import { PlaylistService } from '../../services/admin/playlist.service';
import { SongItemPopupComponent } from '../songitem/songitempopup/songitempopup.component';
import { SongCreditPopupComponent } from '../songitem/songcreditpopup/songcreditpopup.component';
import { PlaylistTablePopupComponent } from '../../../views/admin/playlist/playlistpopup/playlist-table-popup.component';

@Component({
  selector: 'app-bottomplayer',
  templateUrl: './bottomplayer.component.html',
  styleUrls: ['./bottomplayer.component.scss']
})
export class BottomPlayer implements OnInit, OnDestroy {

  wavesurfer;
  id = 'songitem-';
  isLoaded = false;
  isPlaying = false;
  currentTime = '00:00';
  duration = '00:00';

  @Input() songItem: any;
  // @Input() artistItem: any;

  constructor(
    private authService: AuthenticationService,
    private dialog: MatDialog,
    private ngZone: NgZone,
    private router: Router,
    private playerService: PlayerService,
    private loader: AppLoaderService,
    private snack: MatSnackBar,
    private playlistService: PlaylistService
  ) { }

  ngOnInit() {
      var me = this;
      setTimeout(() => {
          me.initPlayer()
      }, 100);

    this.id += randomString();
  }

  initPlayer(){
    var me = this;

    this.wavesurfer = WaveSurfer.create({
      container: '#'+this.id,
      waveColor: '#428bca',
      progressColor: '#31708f',
      height: 50,
      barWidth: 2
    });

    this.wavesurfer.load(this.songItem.song_music);
    this.wavesurfer.on('ready', function () {
        me.isLoaded = true;
        me.wavesurfer.play();
        // me.isPlaying = true;
        me.playerService.isPlaying = true;

        var duration = me.wavesurfer.getDuration();
        var seconds = Math.floor(duration);
        var mins = Math.floor(seconds / 60);
        seconds = seconds % 60;
        me.duration = (mins < 10 ? ('0' + mins) : mins) + ':' + (seconds < 10  ? ('0' + seconds) : seconds);

    });
    this.wavesurfer.on('finish', function() {
        // me.isPlaying = false;
        me.playerService.isPlaying  = false;
    })
    
    this.wavesurfer.on('audioprocess', function(data) {

      me.ngZone.run(() => {
        var seconds = Math.floor(me.wavesurfer.getCurrentTime());
        var mins = Math.floor(seconds / 60);
        seconds = seconds % 60;
        me.currentTime = (mins < 10 ? ('0' + mins) : mins) + ':' + (seconds < 10  ? ('0' + seconds) : seconds);
      });
      

      if(!me.authService.currentUserValue) {
        
        if(me.wavesurfer.getCurrentTime() >=30) {
          me.wavesurfer.pause();
          // me.isPlaying = false;
          me.playerService.isPlaying = false;
          
          me.ngZone.run(() => {
            me.openPopUp();
          })

          console.log(me.wavesurfer.getCurrentTime() );
        }
        
      }
    })
  }

  play() {
    if(this.isLoaded) {
        if(this.playerService.isPlaying) {
          this.wavesurfer.pause();
          // this.isPlaying = false;
          this.playerService.isPlaying = false;
        } else {
          this.wavesurfer.play();
          // this.isPlaying = true;
          this.playerService.isPlaying = true;
        }
    }
  }

  openPopUp() {
    let dialogRef: MatDialogRef<any> = this.dialog.open(SongItemPopupComponent, {
      disableClose: false
    })
  }

  gotoLicenseRequest() {
    this.router.navigate(['/page/licenserequest/' + this.songItem.song_id]);
  }

  ngOnDestroy() {
    this.wavesurfer.unAll();
    this.wavesurfer.destroy();
  }

  downloadLyrics(){
    if(this.songItem.song_music_lyrics) {
      window.open(this.songItem.song_music_lyrics)
    }
  }

  downloadMP3() {
    if(this.songItem.song_music) {
      window.open(this.songItem.song_music)
    }
  }

  downloadInst() {
    if(this.songItem.song_music_inst) {
      window.open(this.songItem.song_music_inst)
    } 
  }

  songCredit() {
    let dialogRef: MatDialogRef<any> = this.dialog.open(SongCreditPopupComponent, {
      disableClose: false,
      width: '300px',
      data: { payload: this.songItem }
    })
  }

  addToPlaylist() {
    this.playerService.addPlaylist(this.songItem);
  }

  clearPlaylist() {
    this.playerService.clearPlaylist();
  }

  playListItem(songItem) {
    var me = this;
    this.playerService.currentSong = null;
    setTimeout(function() {
      me.playerService.currentSong = songItem;
    }, 10)
  }

  deleteListItem(songItem) {
    this.playerService.deletePlayListItem(songItem);
  }

  savePlayList() {
    if(!this.authService.currentUserValue) {
      this.router.navigate(['/sessions/signin']);
    } else {
      this.openPopUpForPlaylist({}, true);
    }
  }

  openPopUpForPlaylist(data: any = {}, isNew?) {
    let title = isNew ? 'Add new Playlist' : 'Update Playlist';
    let dialogRef: MatDialogRef<any> = this.dialog.open(PlaylistTablePopupComponent, {
      width: '720px',
      height: '500px',
      disableClose: true,
      data: { title: title, payload: data }
    })
    dialogRef.afterClosed()
      .subscribe(res => {
        if(!res) {
          // If user press cancel
          return;
        }
        this.loader.open();
        if (isNew) {
          res['songs'] = this.playerService.playlist;
          this.playlistService.addItemWithSongs(res)
            .subscribe(data => {
              this.loader.close();
              this.snack.open('Playlist Added!', 'OK', { duration: 4000 })
              this.playerService.clearPlaylist();
              // this.getItems();
            })
        }
      })
  }
}



