import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
@Component({
  selector: 'songitem-popup',
  templateUrl: './songitempopup.component.html',
  styleUrls: ['./songitempopup.component.scss']
})
export class SongItemPopupComponent implements OnInit {
  public itemForm: FormGroup;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<SongItemPopupComponent>,
    private router: Router
  ) { }

  ngOnInit() {
  }

  close() {
    this.dialogRef.close(true);
  }
  
  gotologin() {
    this.router.navigate(['/sessions/signin']);
  }

  gotoregister() {
    this.router.navigate(['/sessions/signup']);
  }
}



