import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { UserService } from './../../../../shared/services/admin/users.service';
import { PlaylistService } from './../../../../shared/services/admin/playlist.service';

@Component({
  selector: 'app-playlist-table-popup',
  templateUrl: './playlist-table-popup.component.html',
  styleUrls: ['./playlist-table-popup.component.scss']
})
export class PlaylistTablePopupComponent implements OnInit {
  public itemForm: FormGroup;
  is_changed_avatar = false;
  avatar_url = '';
  file;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<PlaylistTablePopupComponent>,
    private fb: FormBuilder,
    private userService: UserService,
    private playlistService: PlaylistService
  ) { }

  ngOnInit() {
    this.buildItemForm(this.data.payload)
  }
  buildItemForm(item) {
    this.avatar_url = item.playlist_thumb;
    this.itemForm = this.fb.group({
      playlist_title: [item.playlist_title || '', Validators.required],
      playlist_note: [item.playlist_note || '', Validators.required],
      playlist_status: [item.playlist_status == 0 ? false : true],
      playlist_thumb: [item.playlist_thumb]
    })
  }

  submit() {
    this.dialogRef.close(this.itemForm.value)
  }

  clickChangeAvatar() {
    document.getElementById('avatar-input').click();
  }

  onFileChange(event) {
    this.is_changed_avatar = true;
    event.preventDefault();
    if (event.target.files.length > 0) {
      this.file = event.target.files[0];
      var reader = new FileReader();
      reader.onload = function(e: any) {
        var avatarImage : any = document.getElementById('avatar_img');
        avatarImage.src = e.target.result;
      }
      reader.readAsDataURL(this.file);
    }
  }

  upload() {
    const formData = new FormData();
    formData.append('playlist_thumb', this.file);

    this.playlistService.uploadPlaylistThumb(formData).subscribe(
      (res: any) => {
        this.is_changed_avatar = false;
        // this.itemForm.value.playlist_thumb = res.url;
        this.itemForm.controls['playlist_thumb'].setValue( res.url);
      },
      (err) => {
        console.log(err)
      }
    );
  }
}



