import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';

@Component({
  selector: 'songcreditpopup',
  templateUrl: './songcreditpopup.component.html',
  styleUrls: ['./songcreditpopup.component.scss']
})
export class SongCreditPopupComponent implements OnInit {
  
  songData;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<SongCreditPopupComponent>,
  ) { 
    this.songData = data.payload;
  }

  ngOnInit() {
  }

}



