import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

interface IMenuItem {
  type: string,       // Possible values: link/dropDown/icon/separator/extLink
  name?: string,      // Used as display text for item and title for separator type
  state?: string,     // Router state
  icon?: string,      // Material icon name
  tooltip?: string,   // Tooltip text 
  disabled?: boolean, // If true, item will not be appeared in sidenav.
  sub?: IChildItem[], // Dropdown items
  badges?: IBadge[]
}
interface IChildItem {
  type?: string,
  name: string,       // Display text
  state?: string,     // Router state
  icon?: string,
  sub?: IChildItem[]
}

interface IBadge {
  color: string;      // primary/accent/warn/hex color codes(#fff000)
  value: string;      // Display text
}

@Injectable()
export class NavigationService {
  constructor() { }

  // CATALOG PLAYLISTS PLACEMENTS NEWS ABOUT MIXES
  defaultMenu: IMenuItem[] = [
    {
      name: 'ARTISTS',
      type: 'link',
      tooltip: 'Catalog',
      // icon: 'dashboard',
      state: 'page/catalog'
    },
    {
      name: 'SONGS',
      type: 'link',
      tooltip: 'Songs',
      // icon: 'dashboard',
      state: 'page/songs'
    },
    {
      name: 'PLAYLISTS',
      type: 'link',
      tooltip: 'Dashboard',
      // icon: 'dashboard',
      state: 'page/playlists'
    },
    {
      name: 'PLACEMENTS',
      type: 'link',
      tooltip: 'PLACEMENTS',
      // icon: 'dashboard',
      state: 'page/placements'
    },
    {
      name: 'NEWS',
      type: 'link',
      tooltip: 'News',
      // icon: 'dashboard',
      state: 'page/news'
    },
    {
      name: 'ABOUT',
      type: 'link',
      tooltip: 'About',
      state: 'page/about'
    },
    {
      name: 'Contact',
      type: 'link',
      tooltip: 'About',
      state: 'page/contact'
    }
  ];

  adminMenu: IMenuItem[] = [
    {
      name: 'DASHBOARD',
      type: 'link',
      tooltip: 'Dashboard',
      // icon: 'dashboard',
      state: 'admin/dashboard'
    },
    {
      name: 'Artists',
      type: 'link',
      tooltip: 'Artists',
      // icon: 'dashboard',
      state: 'admin/artists'
    },
    {
      name: 'Songs',
      type: 'link',
      tooltip: 'Songs',
      // icon: 'dashboard',
      state: 'admin/songs'
    },
    {
      name: 'Users',
      type: 'link',
      tooltip: 'Users',
      // icon: 'dashboard',
      state: 'admin/users'
    },
    {
      name: 'PlayLists',
      type: 'link',
      tooltip: 'Playlists',
      // icon: 'dashboard',
      state: 'admin/playlist'
    },
    {
      name: 'Placements',
      type: 'link',
      tooltip: 'Placements',
      // icon: 'dashboard',
      state: 'admin/placements'
    },
    {
      name: 'News',
      type: 'link',
      tooltip: 'News',
      // icon: 'dashboard',
      state: 'admin/news'
    },
    {
      name: 'About',
      type: 'link',
      tooltip: 'About',
      state: 'admin/about'
    },
  ]


  // Icon menu TITLE at the very top of navigation.
  // This title will appear if any icon type item is present in menu.
  iconTypeMenuTitle: string = 'Frequently Accessed';
  // sets iconMenu as default;
  menuItems = new BehaviorSubject<IMenuItem[]>(this.defaultMenu);
  // navigation component has subscribed to this Observable
  menuItems$ = this.menuItems.asObservable();

  adminMenuItems = new BehaviorSubject<IMenuItem[]>(this.adminMenu);
  adminMenuItems$ = this.adminMenuItems.asObservable();

  // menuItems = new BehaviorSubject<IMenuItem[]>(this.adminMenu);
  // navigation component has subscribed to this Observable
  // menuItems$ = this.menuItems.asObservable();

  // Customizer component uses this method to change menu.
  // You can remove this method and customizer component.
  // Or you can customize this method to supply different menu for
  // different user type.
  publishNavigationChange(menuType: string) {
    this.menuItems.next(this.defaultMenu);
  }
}


