import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from './../authentication.service';

import 'rxjs/add/operator/delay';


@Injectable()
export class NewsService {
  header: HttpHeaders;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {
    this.setHeader();
  }

  setHeader() {
    let currentUser = this.authenticationService.currentUserValue;
    if (currentUser && currentUser.user_token) {
        this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
    }
  }

  getAll() {
    return this.http.get(environment.apiURL + 'admin/news/news');
  }

  getPublished() {
    return this.http.get(environment.apiURL + 'admin/news/newspublished');
  }

  getItem(news_id) {
    return this.http.get(environment.apiURL + 'admin/news/newsitem/' + news_id);
  }

  addItem(data) {
    return this.http.post(environment.apiURL + 'admin/news/news', data, {headers: this.header})
  }

  updateItem(news_id, data) {
    return this.http.patch(environment.apiURL + 'admin/news/news/' + news_id, data, {headers: this.header});
  }

  removeItem(news_id) {
    return this.http.delete(environment.apiURL + 'admin/news/news/' + news_id, {headers: this.header})
  }

  uploadNewsThumb(data) {
    return this.http.post(environment.apiURL + 'admin/news/newsthumb', data, {headers: this.header})
  }

  changeFeature(news_id) {
    return this.http.post(environment.apiURL + 'admin/news/changefeatured/' + news_id, {headers: this.header})
  }
}



