import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as S3 from 'aws-sdk/clients/s3';
import * as randomString from 'random-string';
import 'rxjs/add/operator/delay';

import { environment } from '../../../../environments/environment';
import { AuthenticationService } from '../authentication.service';




@Injectable()
export class DashboardService {
  header: HttpHeaders;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {
    this.setHeader();
  }

  setHeader() {
    let currentUser = this.authenticationService.currentUserValue;
    if (currentUser && currentUser.user_token) {
        this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
    }
  }

  getSlidersHome() {
    return this.http.get(environment.apiURL + 'admin/dashboard/homesliders');
  }

  getAllSliders() {
    return this.http.get(environment.apiURL + 'admin/dashboard/admin_homesliders', {headers: this.header});
  }

  addNewHomeSlider(data) {
    return this.http.post(environment.apiURL + 'admin/dashboard/admin_homeslider', data, {headers: this.header})
  }

  removeSlider(slider_id) {
    return this.http.delete(environment.apiURL + 'admin/dashboard/admin_homeslider/' + slider_id, {headers: this.header})
  }

  toggleShowSlider(slider_id) {
    return this.http.patch(environment.apiURL + 'admin/dashboard/admin_homeslider_toggle/' + slider_id, {headers: this.header})
  }

  changeOrdersSlider(data) {
    return this.http.patch(environment.apiURL + 'admin/dashboard/admin_homeslider_order/', data, {headers: this.header})
  }

  uploadMusicToS3(file) {
    const contentType = file.type;
    const bucket = new S3(
          {
              accessKeyId: environment.accessKeyId,
              secretAccessKey: environment.secretAccessKey,
              region: environment.region
          }
      );
      const params = {
          Bucket: 'music-sync',
          Key: randomString() + '-' + file.name,
          Body: file,
          ACL: 'public-read',
          ContentType: contentType
      };
      return bucket.upload(params);
    }

  addVideo(data) {
    return this.http.post(environment.apiURL + 'admin/dashboard/admin_video', data, {headers: this.header})
  }

  getAllVideos() {
    return this.http.get(environment.apiURL + 'admin/dashboard/admin_videos', {headers: this.header})
  }

  updateVideo(video_id, data) {
    return this.http.patch(environment.apiURL + 'admin/dashboard/admin_video/' + video_id, data, {headers: this.header})
  }

  changeVideoFeature(video_id) {
    return this.http.post(environment.apiURL + 'admin/dashboard/changevideofeatured/' + video_id, {headers: this.header})
  }

  deleteVideo(video_id) {
    return this.http.delete(environment.apiURL + 'admin/dashboard/admin_video/' + video_id, {headers: this.header})
  }

  getMusicReview() {
    return this.http.get(environment.apiURL + 'admin/dashboard/musicreview')
  }

  saveMusicReview(data) {
    return this.http.post(environment.apiURL + 'admin/dashboard/musicreview', data, {headers: this.header})
  }

  getSocial() {
    return this.http.get(environment.apiURL + 'admin/dashboard/sociallinks')
  }

  saveSocial(data) {
    return this.http.post(environment.apiURL + 'admin/dashboard/sociallinks', data, {headers: this.header})
  }

}


