import { Injectable } from '@angular/core';
import * as S3 from 'aws-sdk/clients/s3';
import * as randomString from 'random-string';

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from './../authentication.service';
import 'rxjs/add/operator/delay';


@Injectable()
export class SongsService {
  header: HttpHeaders;

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {
    this.setHeader();
  }

  setHeader() {
    let currentUser = this.authenticationService.currentUserValue;
    if (currentUser && currentUser.user_token) {
        this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
    }
  }

  getAll() {
    return this.http.get(environment.apiURL + 'admin/song/getallsongs');
  }

  getItem(song_id) {
    return this.http.get(environment.apiURL + 'admin/song/song/' + song_id);
  }

  add(data: any) {
    return this.http.post(environment.apiURL + 'admin/song/song/', data, {headers: this.header})
  }

  update(song_id: number, data: any) {
    return this.http.patch(environment.apiURL + 'admin/song/song/' + song_id, data, {headers: this.header});
  }

  uploadThumb(data) {
    return this.http.post(environment.apiURL + 'admin/song/songthumb', data, {headers: this.header})
  }

  uploadMusic(data) {
    return this.http.post(environment.apiURL + 'admin/song/songmusic', data, {headers: this.header})
  }

  uploadMusicToS3(file) {
    const contentType = file.type;
    const bucket = new S3(
          {
              accessKeyId: environment.accessKeyId,
              secretAccessKey: environment.secretAccessKey,
              region: environment.region
          }
      );

      var ext = '.' + file.name.split('.').pop();
      const params = {
          Bucket: 'music-sync',
          Key: randomString() + ext,
          Body: file,
          ACL: 'public-read',
          ContentType: contentType
      };
      // bucket.upload(params, function (err, data) {
      //     if (err) {
      //         console.log('There was an error uploading your file: ', err);
      //         return false;
      //     }
      //     console.log('Successfully uploaded file.', data);
      //     return true;
      // });
      
      //for upload progress   
      /*return bucket.upload(params).on('httpUploadProgress', function (evt) {
          console.log(evt.loaded + ' of ' + evt.total + ' Bytes');
      }).send(function (err, data) {
          if (err) {
              console.log('There was an error uploading your file: ', err);
              return false;
          }
          console.log('Successfully uploaded file.', data);
          return true;
      });*/

      return bucket.upload(params);
    }

    download() {
      return this.http.get(environment.apiURL + 'admin/song/download/0Mrk5utC-good-trouble-picture.jpg');
    }

    remove(song_id) {
      return this.http.delete(environment.apiURL + 'admin/song/song/' + song_id, {headers: this.header})
    }

    changeFeature(song_id) {
      return this.http.post(environment.apiURL + 'admin/song/changefeatured/' + song_id, {headers: this.header})
    }

    licenseRequest(data) {
      return this.http.post(environment.apiURL + 'admin/song/license/', data)
    }
}



