import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { User } from '../../models/user';
import { environment } from '../../../../environments/environment';
import { AuthenticationService } from './../authentication.service';

@Injectable()
export class UserService {

    header: HttpHeaders;
    
    userTypes = [
        {
            'value': '0',
            'type': 'admin'
        }, {
            'value': '1',
            'type': 'general'
        }
    ]

    constructor(
        private http: HttpClient,
        private authenticationService: AuthenticationService
        ) {
        this.setHeader();
    }

    setHeader() {
        let currentUser = this.authenticationService.currentUserValue;
        if (currentUser && currentUser.user_token) {
            this.header = new HttpHeaders().set('Authorization', currentUser.user_token);
        }
    }

    getAll() {
        return this.http.get<User[]>(environment.apiURL + 'admin/user/getallusers', {headers: this.header});
    }

    update(user_id: number, data: any) {
        return this.http.patch(environment.apiURL + 'admin/user/user/' + user_id, data, {headers: this.header});
    }

    add(data: any) {
        return this.http.post(environment.apiURL + 'admin/user/user/', data, {headers: this.header})
    }

    remove(user_id: number) {
        return this.http.delete(environment.apiURL + 'admin/user/user/' + user_id, {headers: this.header})
    }

    uploadAvatar(data) {
        return this.http.post(environment.apiURL + 'admin/user/useravatar', data, {headers: this.header})
    }


}


